body {
  background-color: #66a3ff;
}

.app {
  background-color: #3f3166;
  min-height: 800px;
  width: 600px;
  margin: 40px auto;
}

#todo-form input {
  background-color: rgb(27, 120, 137);
  border: 0;
  width: 400px;
  height: 50px;
  padding: 0 30px;
  margin: 20px;
  font-size: 18px;
  border-radius: 10px;
  color: white;
}

#todo-form input::focus {
  outline: none;
}

#todo-form input::placeholder {
  color: rgba(255, 255, 255, 0.842);
}


#todo-form button {
  height: 50px;
  width: 80px;
  border-radius: 5px;
  border: 0;
  background-color: rgb(202,156,24);
  cursor: pointer;
  font-weight: bolder;
  color: #fff;
}
