.list {
    color: #fff;
    width: 500px;
    background-color: #bf07ac;
    margin: 20px auto;
    font-size: 18px;
    height: 50px;
    border-radius: 10px;
    font-weight: bolder;
}

.list p {
    padding: 13px;
    position: relative;
}

.list p span {
    position: absolute;
    right: 10px;
    cursor: pointer;
}

.faicons {
    margin: 0 5px;
    font-size: 15px;
}

.list p input {
    /* background-color: transparent; */
    border: 0;
    font-size: 18px;
    width: 435px;
    cursor: pointer;
    margin: 0;
    border-radius: 5px;
}

.list p input:focus {
    outline: none;
}